.place {
  &__photo-container {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    max-height: 256px;
    overflow: hidden;
  
    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    }
  
    @include onDesktop() {
      height: 256px;
      padding-bottom: 0;
    }
  }
  
  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 8px;
    object-fit: cover;
    object-position: center;
    z-index: 1;
    transition-duration: 300ms;
    transition-property: transform;

    &:hover {
      transform: scale(1.2);
    }
  }
  
  &__photos {
    margin-bottom: 16px;
  
    @include onDesktop {
      margin-bottom: 24px;
    }
  }
  
  &__link {
    color: $colorBlue;
    text-decoration: none;
    font-weight: bold;
  }
}
